.movieDetail {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.movieBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #32363a;
  margin-top: 40px;
  margin-left: 800px;
  margin-right: 800px;
}

.img {
  border-radius: 30px;
  border: 5px solid #fefefe;
  width: 400px;
  height: 400px;
}

.info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin-top: 40px;
}

.title {
  font-size: 20px;
  border: 1px solid #fefefe;
  border-radius: 20px;
  padding: 8px 8px;
}

.movieinfo {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;
}

.icon {
  font-size: 30px;
  margin-top: 20px;
}
